.main-image {
  background-image: url(https://hips.hearstapps.com/hmg-prod.s3.amazonaws.com/images/ghi-best-comfortable-shoes-1596050818.png);
  width: 100%;
  height: 653px;
  background-attachment: fixed;
  background-size: cover;
}
.img-content {
  position: absolute;
  top: 50%;
  left: 17%;
  transform: translate((-50%, -50%));
}
.p {
  font-size: 12px;
  font-weight: 900;
}
h2 {
  font-size: 45px;
  font-weight: 900;
}

@media (max-width: 900px) {
  .main-image .img-content {
    left: 25%;
  }
}

@media (max-width: 600px) {
  .main-image .img-content {
    left: 30%;
  }
}

@media (max-width: 400px) {
  .main-image .img-content {
    left: 35%;
  }
}
